* {
   box-sizing: border-box;
}

body {
   font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
      Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
   -webkit-font-smoothing: antialiased;
   -moz-osx-font-smoothing: grayscale;
   font-size: 1em;
   line-height: 1.5;
   color: #000000;
   margin: 0;
}

a {
   text-decoration: none;
}

p {
   margin: 0;
}

.visualy-hidden {
   position: absolute;
   width: 1px;
   height: 1px;
   margin: -1px;
   padding: 0;
   overflow: hidden;
   border: 0;
   clip: rect(0 0 0 0);
}

.search-container {
   width: 100%;
   padding-top: 5px;
   padding-bottom: 5px;
   position: fixed;
   background-color: #fa8072;
   box-shadow: 0px 2px 5px 0px #ffa07a;
}

.search-form {
   width: 100%;
   display: flex;
   justify-content: center;
}

.search-form input {
   width: 30%;
   border-radius: 4px 0 0 4px;
   border: 1px solid #ffe4e1;
   outline: none;
   background-color: #fff;
   padding: 8px 10px;
   font: inherit;
}

.search-form button {
   display: flex;
   justify-content: center;
   align-items: center;
   min-width: 40px;
   border: none;
   background-color: #ffe4e1;
   border-radius: 0 4px 4px 0;
   cursor: pointer;
   transition: all 0.5s;
}

.search-form button:hover,
.search-form button:focus {
   background-color: #fff5ee;
   transition: all 0.5s;
}

.gallery-container {
   padding-left: 15px;
   padding-right: 15px;
   padding-bottom: 25px;

   @media screen and (min-width: 480px) {
      width: 480px;
      margin-left: auto;
      margin-right: auto;
   }

   @media screen and (min-width: 768px) {
      width: 768px;
   }

   @media screen and (min-width: 1440px) {
      width: 1440px;
   }
}

.gallery,
.info {
   display: flex;
   flex-wrap: wrap;
}

.gallery {
   margin-left: -20px;
   padding-top: 60px;
   justify-content: center;

}

.gallery-link {
   margin-top: 20px;
   margin-left: 20px;
   min-width: 320px;
   flex-basis: calc((100% - 80px) / 4);
   border: 1px solid rgb(241, 238, 238);
   border-radius: 10px;
   overflow: hidden;
   transition: all 0.3s;
}

.gallery-link:hover,
.gallery-link:focus {
   box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
}

.photo-card {
   display: flex;
   flex-direction: column;
   justify-content: space-evenly;
}

.photo-card-image {
   display: block;
   height: 30vh;
   object-fit: cover;
}

.info {
   padding: 10px;
   justify-content: space-around;
}

.info-item {
   display: flex;
   flex-direction: column;
   align-items: center;
   color: black;
}

.info-item:not(:first-child) {
   margin-left: 5px;
}